import { SimulationModel } from './../../models/simulation.model';
import { items } from './../../../../../mock-api/apps/file-manager/data';
import { Component, OnInit, Inject } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SimulationsService } from '../../services/simulations.service';
import { CurrencyPipe } from '@angular/common';

@Component({
  selector: 'app-simulation-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class SimulationAddComponent implements OnInit {

  supportForm: FormGroup;
  states: any[];

  constructor(
    private _formBuilder: FormBuilder,
    public matDialogRef: MatDialogRef<any>,
    private service: SimulationsService,
    private currencyPipe: CurrencyPipe,
    @Inject(MAT_DIALOG_DATA) public simulation: SimulationModel
  ) { }

  ngOnInit(): void {
    this.supportForm = this._formBuilder.group({
        name   : [this.simulation.name, Validators.required],
        email  : [this.simulation.email, [Validators.required, Validators.email]],
        ddi    : '',
        ddd    : '',
        phone    : this.simulation.phone,
        cpf    : this.simulation.cpf,
        value    : this.currencyPipe.transform(this.simulation.budget, 'BRL'),
        type    : this.simulation.typeName,
        productivity    : this.simulation.bags,
        areaWeidth    : this.simulation.area,
        cep    : this.simulation.cep,
        address    : this.simulation.address,
        addressNumber    : this.simulation.addressNumber,
        city    : this.simulation.city,
        uf   : this.simulation.state,
    });
    // this.service.getStates().subscribe((items) => {
    //     this.states = items;
    // });
  }

  cancel(): void {
    this.matDialogRef.close();
  }


}
