import { MatPaginatorIntl } from '@angular/material/paginator';

export class CustomPaginationIntl {
    getCustomPaginationIntl(): MatPaginatorIntl {
        const customPaginatorIntl = new MatPaginatorIntl();
        customPaginatorIntl.itemsPerPageLabel = 'Resultados por pgina';
        customPaginatorIntl.firstPageLabel = 'Primeira pgina';
        customPaginatorIntl.lastPageLabel = 'ltima pgina';
        customPaginatorIntl.nextPageLabel = 'Prxima pgina';
        customPaginatorIntl.previousPageLabel = 'Pgina anterior';
        customPaginatorIntl.getRangeLabel = this.getRangeLabel.bind(this);
        return customPaginatorIntl;
    }

    private getRangeLabel = (page: number, pageSize: number, length: number): string => {
        if (length == 0 || pageSize == 0) { return `0 de ${length}`; }

        length = Math.max(length, 0);

        const startIndex = page * pageSize;

        const endIndex = startIndex < length ?
            Math.min(startIndex + pageSize, length) :
            startIndex + pageSize;

        return `Mostrando entre ${startIndex + 1} e ${endIndex} resultados de ${length}`;
      }

}
