import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BehaviorSubject, Observable, of, take, tap, map, catchError } from 'rxjs';
import { environment as env} from 'environments/environment';
import { CultureMapper } from '../mapper/culture.mapper';
import { ApiResponseModel } from 'app/shared/model/response.model';
import { CultureResponseModel } from '../models/culture.response.model';
import { CultureModel } from '../models/culture.model';

@Injectable()
export class CulturesService {

  private host: string;

  private _cultures: BehaviorSubject<CultureModel[] | null> = new BehaviorSubject(null);

  constructor(
      private http: HttpClient,
      private mapper: CultureMapper
      ) {
    this.host = env.host;
    this.getCultures().pipe(take(1)).subscribe();
  }

  get cultures$(): Observable<CultureModel[]> {
    return this._cultures.asObservable();
  }

  getCultures(): Observable<ApiResponseModel<CultureResponseModel>> {
    return this.http.get<ApiResponseModel<CultureResponseModel>>(this.host + '/crop/type?order[name]=asc')
        .pipe(
            take(1),
            tap((values: ApiResponseModel<CultureResponseModel>) => {
                const resp = this.mapper.mapResponseToCultures(values);
                this._cultures.next(resp);
            })
        );
  }

}
