import { StringFormatHelper } from './../../../../shared/helpers/string-format.helper';
import { Injectable } from '@angular/core';
import { SimulationResponseModel } from '../models/simulation.response.model';
import { Builder } from 'builder-pattern';
import { SimulationModel } from '../models/simulation.model';
import { ApiResponseModel } from 'app/shared/model/response.model';
import { DateParserHelper } from 'app/shared/helpers/data-parser.helper';
import { StatusParserHelper } from 'app/shared/helpers/status-parser.helper';
import { CulturesService } from '../services/cultures.service';
import { CultureModel } from '../models/culture.model';


@Injectable()
export class SimulationMapper {

    private cultures: CultureModel[] = [];

    constructor(
        private cultureService: CulturesService
    ) {
        this.cultureService.cultures$.subscribe((resp: CultureModel[]) => this.cultures = resp);
    }

    mapResponseToSimulation(apiResponse: SimulationResponseModel): SimulationModel {
        return Builder<SimulationModel>()
            .id(apiResponse.id)
            .name(apiResponse.name)
            .address(apiResponse.address)
            .addressNumber(apiResponse.number.toString())
            .area(apiResponse.area)
            .bags(apiResponse.bags)
            .budget(apiResponse.budget)
            .cep(StringFormatHelper.formatCep(apiResponse.cep))
            .city(apiResponse.city)
            .cpf(StringFormatHelper.formatCpf(apiResponse.cpf))
            .createdAt(DateParserHelper.parseToStr(apiResponse.createdAt))
            .email(apiResponse.email)
            .phone(StringFormatHelper.formatPhone(apiResponse.phone))
            .state(apiResponse.state)
            .status(StatusParserHelper.parseToStatus(apiResponse.status))
            .type(apiResponse.type)
            .typeName(this.getCultureName(apiResponse.type))
            .updatedAt(DateParserHelper.parseToStr(apiResponse.createdAt))
            .build();
    }

    mapResponseToSimulations( apiResponseArray: ApiResponseModel<SimulationResponseModel>): SimulationModel[] {
        return apiResponseArray.data.rows.map((apiResponse: SimulationResponseModel) => this.mapResponseToSimulation(apiResponse));
    }

    private getCultureName(id: string): string {
        return this.cultures.find((item: CultureModel) => item.id.toString() === id).name;
    }

}
