import { Component, Inject, Input, OnInit } from '@angular/core';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { SimulationModel } from '../../models/simulation.model';
import { SimulationsService } from '../../services/simulations.service';

@Component({
  selector: 'app-simulation-resume',
  templateUrl: './resume.component.html',
  styleUrls: ['./resume.component.scss']
})
export class SimulationResumeComponent implements OnInit {

  constructor(
    public matDialogRef: MatDialogRef<any>,
    @Inject(MAT_DIALOG_DATA) public simulation: SimulationModel
  ) { }

  ngOnInit(): void {

  }

  aprove(): void {
    this.matDialogRef.close({ action: 'aproved' });
  }

  reprove(): void {
    this.matDialogRef.close({ action: 'reproved' });
  }

}
