import { MatDialog } from '@angular/material/dialog';
import { Component, OnInit, ViewEncapsulation } from '@angular/core';
import { SimulationModel } from '../models/simulation.model';
import { SimulationResumeComponent } from './resume/resume.component';
import { SimulationAddComponent } from './add/add.component';
import { SimulationSelectedTab } from '../enums/selected.enum';

@Component({
  selector: 'app-container',
  templateUrl: './container.component.html',
  styleUrls: ['./container.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class SimulationsContainerComponent implements OnInit {

  selectedTab: SimulationSelectedTab = SimulationSelectedTab.TAB_LEAD;

  constructor(
    private _matDialog: MatDialog
  ) { }

  ngOnInit(): void {
  }

  generateSimulation(): void {
    this._matDialog.open(SimulationAddComponent, {
        maxWidth: '100vw',
        maxHeight: '100vh',
        height: '600px',
        width: '1024px',
        autoFocus   : false,
    });
  }

  selectTab($event: any): void {
    this.selectedTab = $event.index;
  }

  isTabLeadSelected(): boolean {
    return this.selectedTab === SimulationSelectedTab.TAB_LEAD;
  }

  isTabClientSelected(): boolean {
    return this.selectedTab === SimulationSelectedTab.TAB_CLIENT;
  }

  isTabReprovedSelected(): boolean {
    return this.selectedTab === SimulationSelectedTab.TAB_REPROVED;
  }
}
