import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { BehaviorSubject, Observable, of, take, tap, map, catchError } from 'rxjs';
import { environment as env} from 'environments/environment';
import { SimulationMapper } from '../mapper/simulation.mapper';
import { ApiResponseModel } from 'app/shared/model/response.model';
import { SimulationResponseModel } from '../models/simulation.response.model';
import { SimulationModel } from '../models/simulation.model';

@Injectable()
export class SimulationsService {

  private host: string;

  private _simulations: BehaviorSubject<SimulationModel[] | null> = new BehaviorSubject(null);

  constructor(
      private http: HttpClient,
      private mapper: SimulationMapper
      ) {
    this.host = env.host;
  }

  get simulations$(): Observable<SimulationModel[]> {
    return this._simulations.asObservable();
  }

  getSimulations(options: any): Observable<ApiResponseModel<SimulationResponseModel>> {
    const offset = (options.pagination.page * options.pagination.size);
    const limit = options.pagination.size;
    const filterName = (options.filter) ? '&filter[name.startsWith]=' + options.filter : '';
    const filterStatus = (options.status) ? '&filter[status]=' + options.status : '';
    return this.http.get<ApiResponseModel<SimulationResponseModel>>(this.host + '/planning?order[createdAt]=desc&offset=' + offset + '&limit=' + limit + filterName + filterStatus)
        .pipe(
            take(1),
            tap((values: ApiResponseModel<SimulationResponseModel>) => {
                const resp = this.mapper.mapResponseToSimulations(values);
                this._simulations.next(resp);
            })
        );
  }

  getStates(): Observable<any[]> {
    return this.http.get<any>(this.host + '/state');
  }

  aproveReproveSimulation(id: number, status: string): Observable<any> {
    const options = {
        headers: new HttpHeaders().set('Content-Type', 'application/x-www-form-urlencoded')
    };

    const body = new URLSearchParams();
    body.set('status', status);

    return this.http.put(this.host + '/planning/status/' + id.toString(), body.toString(), options )
        .pipe(
            take(1),
            catchError((erro: any) => { console.error('Deu erro', erro); return of(erro); })
        );
  }

}
