import { ApiResponseModel } from 'app/shared/model/response.model';
import { SimulationAddComponent } from './../add/add.component';
import { MatDialog } from '@angular/material/dialog';
import { SimulationAddContractComponent } from './../add-contract/add-contract.component';
import {
    Observable,
    take,
    Subject,
    debounceTime,
    takeUntil,
    map,
    switchMap,
    tap,
    merge,
} from 'rxjs';
import {
    Component,
    OnDestroy,
    OnInit,
    ViewChild,
    EventEmitter,
    ChangeDetectorRef,
    Input,
} from '@angular/core';
import { SimulationsService } from '../../services/simulations.service';
import { SimulationModel } from '../../models/simulation.model';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { FormControl } from '@angular/forms';
import { SimulationResponseModel } from '../../models/simulation.response.model';
import { StatusEnum } from 'app/shared/enums/status.enum';
import { SimulationResumeComponent } from '../resume/resume.component';
import { SimulationSelectedTab } from '../../enums/selected.enum';

export interface Pagination {
    length: number;
    size: number;
    page: number;
    lastPage: number;
    startIndex: number;
    endIndex: number;
}

@Component({
    selector: 'app-simulation-list',
    templateUrl: './list.component.html',
    styleUrls: ['./list.component.scss'],
})
export class SimulationListComponent implements OnInit, OnDestroy {
    @Input() selectedTab: SimulationSelectedTab;
    @ViewChild(MatPaginator) private _paginator: MatPaginator;
    @ViewChild(MatSort) private _sort: MatSort;
    search: EventEmitter<any> = new EventEmitter<any>();

    searchControl: FormControl = new FormControl();

    isLoading = false;

    pagination: Pagination = {
        length: 10,
        size: 10,
        page: 0,
        lastPage: 10,
        startIndex: 1,
        endIndex: 10,
    };

    simulations$: Observable<SimulationModel[]>;

    private _unsubscribeAll: Subject<any> = new Subject<any>();

    constructor(
        private service: SimulationsService,
        private _matDialog: MatDialog,
        private _changeDetectorRef: ChangeDetectorRef,
    ) {}

    ngOnInit(): void {

        this.simulations$ = this.service.simulations$;
        this.service
            .getSimulations({
                pagination: this.pagination,
                status: this.getFilterStatus(),
            })
            .pipe(
                take(1),
                tap((value) => {
                    this.pagination.length = value.data.count;
                })
            )
            .subscribe((value) => {
                this.isLoading = false;
            });

        setTimeout(() => {
            this.prepareFiltersAndPaginators();
        }, 1000);
    }

    ngOnDestroy(): void {
        this._unsubscribeAll.next(null);
        this._unsubscribeAll.complete();
    }

    trackByFn(index: number, item: any): any {
        return item.id || index;
    }

    addContract(): void {
        this._matDialog.open(SimulationAddContractComponent, {
            maxWidth: '100vw',
            maxHeight: '100vh',
            height: '600px',
            width: '1024px',
            autoFocus: false,
        });
    }

    editSimulation(item: SimulationModel): void {
        this._matDialog.open(SimulationAddComponent, {
            maxWidth: '100vw',
            maxHeight: '100vh',
            height: '550px',
            width: '1024px',
            autoFocus: false,
            data: item,
        });
    }

    confirmSimulation(item: SimulationModel): void {
        const dialogRef = this._matDialog.open(SimulationResumeComponent, {
            maxWidth: '100vw',
            maxHeight: '100vh',
            height: '600px',
            width: '650px',
            autoFocus: false,
            data: item,
        });

        dialogRef.afterClosed().subscribe((result) => {
            if (result?.action) {
                this.service
                    .aproveReproveSimulation(item.id, result.action)
                    .pipe(take(1))
                    .subscribe(() => {
                        this.service
                            .getSimulations({
                                pagination: this.pagination,
                                status: this.getFilterStatus(),
                            })
                            .pipe(
                                take(1),
                                tap((value) => {
                                    this.pagination.length = value.data.count;
                                })
                            )
                            .subscribe();
                    });
            }
        });
    }

    isLead(simulation: SimulationResponseModel): boolean {
        return simulation.status === StatusEnum.RECEIVED;
    }

    private prepareFiltersAndPaginators(): void {
        this.searchControl.valueChanges
            .pipe(debounceTime(250), takeUntil(this._unsubscribeAll))
            .subscribe((value) => {
                this.search.next({ searchText: value });
            });

        if (this._sort && this._paginator) {
            this._sort.sort({
                id: 'created_at',
                start: 'desc',
                disableClear: true,
            });
            this._paginator.pageIndex = 0;
            this._paginator.pageSize = 10;

            merge(this._sort.sortChange, this.search)
                .pipe(takeUntil(this._unsubscribeAll))
                .subscribe(() => {
                    this.pagination.page = 0;
                });
            merge(this.search, this._paginator.page, this._sort.sortChange)
                .pipe(
                    takeUntil(this._unsubscribeAll),
                    switchMap((value) => {
                        this.isLoading = true;
                        if (value.pageIndex !== undefined) {
                            this.pagination.page = value.pageIndex;
                            this.pagination.size = value.pageSize;
                        }
                        this._changeDetectorRef.markForCheck();
                        return this.service
                            .getSimulations({
                                pagination: this.pagination,
                                status: this.getFilterStatus(),
                                filter: value.searchText,
                            })
                            .pipe(
                                tap((simulation: ApiResponseModel<SimulationResponseModel>) => {
                                    this.pagination.length = simulation.data.count;
                                })
                            );
                    }),
                    map(() => {
                        this.isLoading = false;
                    })
                )
                .subscribe();
        }
    }

    private getFilterStatus(): string {
        if (this.selectedTab === SimulationSelectedTab.TAB_LEAD) {
            return 'RECEIVED';
        }
        if (this.selectedTab === SimulationSelectedTab.TAB_CLIENT) {
            return 'APROVED';
        }
        if (this.selectedTab === SimulationSelectedTab.TAB_REPROVED) {
            return 'REPROVED';
        }
    }
}
