import { Injectable } from '@angular/core';
import { CultureResponseModel } from '../models/culture.response.model';
import { Builder } from 'builder-pattern';
import { CultureModel } from '../models/culture.model';
import { ApiResponseModel } from 'app/shared/model/response.model';


@Injectable()
export class CultureMapper {

    constructor() {}

    mapResponseToCulture(apiResponse: CultureResponseModel): CultureModel {
        return Builder<CultureModel>()
            .id(apiResponse.id)
            .name(apiResponse.name)
            .build();
    }

    mapResponseToCultures( apiResponseArray: ApiResponseModel<CultureResponseModel>): CultureModel[] {
        return apiResponseArray.data.rows.map((apiResponse: CultureResponseModel) => this.mapResponseToCulture(apiResponse));
    }

}
